
import { defineComponent } from 'vue'

export default defineComponent({
    props: {
        textPlaceholder: {
            required: true,
            type: String
        },
        typeInput: {
            required: true,
            type: String
        }
    },
    methods: {
    onChangeInputChild(event: any) {
      this.$emit('changeInput', event.target.value)
    },
  },
   
})
